  --button-disabled-border-bg-color: #ababab;
  --button-default-text-icon-color: #131313;
  --button-focus-text-color: #fcfcfc;
  --button-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.form__btn, .button, .form-products__btn, .button-location, .burger-menu__order-button, .header-modal-button {
  height: 42px;
  background-color: var(--button-default-border-bg-color);
  color: var(--button-focus-text-icon-color);
  cursor: pointer;
  transition: background-color 250ms var(--button-timing-function), color 250ms var(--button-timing-function);
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 10px;
  outline: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.28571;
  display: flex;
  position: relative;
}

.form__btn:hover, .button:hover, .form-products__btn:hover, .button-location:hover, .burger-menu__order-button:hover, .header-modal-button:hover {
  background-color: var(--button-hover-focus-border-bg-color);
}

.form__btn:focus, .button:focus, .form-products__btn:focus, .button-location:focus, .burger-menu__order-button:focus, .header-modal-button:focus {
  background-color: var(--button-hover-focus-border-bg-color);
  color: var(--button-focus-text-color);
}

.form__btn:disabled, .button:disabled, .form-products__btn:disabled, .button-location:disabled, .burger-menu__order-button:disabled, .header-modal-button:disabled {
  background-color: var(--button-disabled-border-bg-color);
  border: none;
}

.form__btn:focus:after, .button:focus:after, .form-products__btn:focus:after, .button-location:focus:after, .burger-menu__order-button:focus:after, .header-modal-button:focus:after {
  opacity: 0;
}

.form__btn:focus:before, .button:focus:before, .form-products__btn:focus:before, .button-location:focus:before, .burger-menu__order-button:focus:before, .header-modal-button:focus:before {
  opacity: 1;
}

.button-franchise {
  height: 42px;
  border: 2px solid var(--button-default-border-bg-color);
  color: var(--button-focus-text-icon-color);
  cursor: pointer;
  transition: border-color 250ms var(--button-timing-function), color 250ms var(--button-timing-function);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 10px;
  outline: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.28571;
  display: flex;
  position: relative;
}

.button-franchise:hover {
  border-color: var(--button-hover-focus-border-bg-color);
}

.button-franchise:focus {
  border-color: var(--button-hover-focus-border-bg-color);
  color: var(--button-focus-text-color);
}

.button-franchise:disabled {
  border-color: var(--button-disabled-border-bg-color);
  background-color: rgba(0, 0, 0, 0);
}

.button-franchise:focus:after {
  opacity: 0;
}

.button-franchise:focus:before {
  opacity: 1;
}

.button-franchise:before, .button-location:before, .burger-menu__order-button:before, .header-modal-button:before {
  content: "";
  width: 12px;
  height: 12px;
  opacity: 0;
  background-image: url("btn-icon-white.30940be4.svg");
  background-repeat: no-repeat;
  background-size: contain;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 82%;
  transform: translate(-50%, -50%);
}

.button-franchise:after, .button-location:after, .burger-menu__order-button:after, .header-modal-button:after {
  content: "";
  width: 12px;
  height: 12px;
  opacity: 1;
  background-image: url("btn-icon.f30ee100.svg");
  background-repeat: no-repeat;
  background-size: contain;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 82%;
  transform: translate(-50%, -50%);
}

